import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery';
import './questionStyle.css';

class Integral extends Component{
    render(){
        $('body').css('background','#f5f5f5')
        $('title').html('')
        return(
            <div>
                <div className="Header">
                    <div className="timeTxT">
                        <Link className="icon-direction_l" to='/user/service'></Link>
                        <div></div>
                    </div>
                </div>
                <div className="question">
                    <h3>Q1: </h3>
                    <p></p>
                    <ul>
                        <li></li>
                        <li>2000</li>
                    </ul>
                    <h3>Q2: </h3>
                    <p></p>
                    <h3>Q3: </h3>
                    <ul>
                        <li>/24,</li>
                        <li></li>
                        <li>1515</li>
                        <li>/</li>
                        <li></li>
                    </ul>
                </div>
            </div>
        )
    }
}
export default Integral;



