import React,{Component} from 'react';
import ReactSwipe from 'react-swipe';
class Slider extends Component{
    constructor(){
        super();
        this.state = {
            index:0
        }
    }
    render(){
        let opt = {
            auto:1600,
            callback:(index) => {
                this.setState({
                    index: index
                })
                //console.log(index)
            }
        }
        return(
            <div className="zSort">
                {/* className="zForm" */}
                <ReactSwipe  swipeOptions={opt}>
                    <ul className="zFormLeft">
                        <li><img src="https://i.loli.net/2017/08/26/59a04aa319a9f.jpg" alt="img"/><span></span></li>
                        <li><img src="https://i.loli.net/2017/08/26/59a04aad67856.jpg" alt="img"/><span></span></li>
                        <li><img src="https://i.loli.net/2017/08/26/59a04aa2f1f9a.jpg" alt="img"/><span></span></li>
                        <li><img src="https://i.loli.net/2017/08/26/59a04aad6d426.jpg" alt="img"/><span></span></li>
                        <li><img src="https://i.loli.net/2017/08/26/59a04aa30af40.jpg" alt="img"/><span></span></li>
                        <li><img src="https://i.loli.net/2017/08/26/59a04aad600fb.jpg" alt="img"/><span></span></li>
                        <li><img src="https://i.loli.net/2017/08/26/59a04aad73a44.jpg" alt="img"/><span></span></li>
                        <li><img src="https://i.loli.net/2017/08/26/59a04aad91a01.jpg" alt="img"/><span></span></li>
                    </ul>
                    <ul className="zFormRight">
                        <li><img src="https://i.loli.net/2017/08/26/59a04aad70a9a.jpg" alt="img"/><span></span></li>
                        <li><img src="https://i.loli.net/2017/08/26/59a04aa306849.jpg" alt="img"/><span></span></li>
                        <li><img src="https://i.loli.net/2017/08/26/59a04aa31237c.jpg" alt="img"/><span></span></li>
                        <li><img src="https://i.loli.net/2017/08/26/59a04aad655f0.jpg" alt="img"/><span></span></li>
                        <li><img src="https://i.loli.net/2017/08/26/59a04aa31cff1.jpg" alt="img"/><span></span></li>
                        <li><img src="https://i.loli.net/2017/08/26/59a04aad533c4.jpg" alt="img"/><span></span></li>
                        <li><img src="https://i.loli.net/2017/08/26/59a04aa3185a4.jpg" alt="img"/><span></span></li>
                        <li><img src="https://i.loli.net/2017/08/26/59a04aa303b84.jpg" alt="img"/><span></span></li>
                    </ul>
                    <ul className="zFormRight">
                        <li><img src="https://i.loli.net/2017/08/26/59a04aad70a9a.jpg" alt="img"/><span></span></li>
                        <li><img src="https://i.loli.net/2017/08/26/59a04aa306849.jpg" alt="img"/><span></span></li>
                        <li><img src="https://i.loli.net/2017/08/26/59a04aa31237c.jpg" alt="img"/><span></span></li>
                        <li><img src="https://i.loli.net/2017/08/26/59a04aad655f0.jpg" alt="img"/><span></span></li>
                        <li><img src="https://i.loli.net/2017/08/26/59a04aa31cff1.jpg" alt="img"/><span></span></li>
                        <li><img src="https://i.loli.net/2017/08/26/59a04aad533c4.jpg" alt="img"/><span></span></li>
                        <li><img src="https://i.loli.net/2017/08/26/59a04aa3185a4.jpg" alt="img"/><span></span></li>
                        <li><img src="https://i.loli.net/2017/08/26/59a04aa303b84.jpg" alt="img"/><span></span></li>
                    </ul>
                </ReactSwipe>
                <div className="zSwitch">
                    <span className={this.state.index === 0 ? 'active' :''}></span>
                    <span className={this.state.index === 1 ? 'active' :''}></span>
                    <span className={this.state.index === 2 ? 'active' :''}></span>
                </div>
            </div>
        )
    }
    //
    componentDidMount() {
        //console.log(ReactSwipe)
    }
}
export default Slider;



