import React,{Component} from 'react';

class MerchantLi extends Component{
    render(){
        let {type,description,open,background} = this.props;
        return(
            <div>
                <div className={open}>
                    <i className={background}>{type}</i>
                    <span className="activity">
                        <span>{description}</span>
                    </span>
                </div>
                {/* <div className="zDis">
                    <i className="activity_2"></i>
                    <span className="activity">
                        <span>201030185030</span>
                    </span>
                </div>
                <div className={open}>
                    <i className="activity_2"></i>
                    <span className="activity">
                        <span>5</span>
                    </span>
                </div>
                <div className={open}>
                    <i className="activity_3"></i>
                    <span className="activity">
                        <span>100.01</span>
                    </span>
                </div>
                <div className={open}>
                    <i className="activity_4"></i>
                    <span className="activity">
                        <span></span>
                    </span>
                </div> */}
            </div>
        )
    }
}
export default MerchantLi;



