import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery';
import '../../css/indexStyle.css';
import Footer from '../footer/footer';
import Slider from './slider';
import Data from '../data/data';
import Merchant from './merchant';

class Home extends Component{
    constructor(){
        super();
        this.state = {
            data:Data
        }
    }
    render(){
        $('body').css('background','#f5f5f5')
        $('title').html('')
        let {data} = this.state;
        let merchantList = null;
        merchantList = data.map((e,i)=>{
            let data = {
                key:i,
                // 
                name:e.seller.name,
                // 
                avatar:e.seller.avatar,
                // 
                description:e.seller.description,
                // 
                deliveryTime:e.seller.deliveryTime,
                // 
                score:e.seller.score,
                // 
                percentage:Math.round(e.seller.score/5*100),
                // 
                sellCount:e.seller.sellCount,
                // 
                minPrice:e.seller.minPrice,
                // 
                ratingCount:e.seller.ratingCount,
                // 
                deliveryPrice:e.seller.deliveryPrice,
                // 
                distance:e.seller.distance,
                // 
                supports:e.seller.supports,
                // 
                num:e.seller.supports.length,
            }
            return <Merchant {...data}/>
        })
        return(
            <div>
                <div className="zCon">
                    <div className="zHeader">
                        <div className="zSeat">
                            <Link className="zLocate" to='/location'>
                                <i className="icon-location zIcon"></i>
                                <span className="zPlace"></span>
                                <i className="icon-triangle zIcon"></i>
                            </Link>
                            <Link className="zLogin" to='/login'>
                                <span> | </span>
                            </Link>
                        </div>
                        <Link to='/search'>
                            <input type="text" className="zText" placeholder="" />
                        </Link>
                    </div>
                    <div className="content">
                        <Slider/>
                        <h3 className="zShops"></h3>
                        <ul className="zSection">
                            {/* <li>
                                <div className="li_left">
                                    <div className="shop_log">
                                        <img src="https://i.loli.net/2017/08/26/59a04aad7932e.jpg" alt=""/>
                                    </div>
                                </div>
                                <div className="li_right">
                                    <div className="zItem">
                                        <div className="username">
                                            <span></span>
                                        </div>
                                        <div className="bill">
                                            <span></span>
                                            <span></span>
                                        </div>
                                    </div>
                                    <div className="zItem zItem_mt">
                                        <div className="shop_star">
                                            <div className="star">
                                                <div className="gray">
                                                    <i className="icon-star"></i>
                                                    <i className="icon-star"></i>
                                                    <i className="icon-star"></i>
                                                    <i className="icon-star"></i>
                                                    <i className="icon-star"></i>
                                                </div>
                                                <div className="yellow">
                                                    <i className="icon-star"></i>
                                                    <i className="icon-star"></i>
                                                    <i className="icon-star"></i>
                                                    <i className="icon-star"></i>
                                                    <i className="icon-star"></i>
                                                </div>
                                            </div>
                                            <span className="grade">4.7</span>
                                            <span>100</span>
                                        </div>
                                        <div className="distribution">
                                            <span></span>
                                        </div>
                                    </div>
                                    <div className="zItem zItem_mt">
                                        <div className="delivery">
                                            <span>20</span>
                                            <i>/</i>
                                            <span>5</span>
                                            <i>/</i>
                                            <span>15/</span>
                                        </div>
                                        <div className="zTime">
                                            <span>1.10km</span>
                                            <i>/</i>
                                            <span className="blue">20</span>
                                        </div>
                                    </div>
                                    <div className="discount">
                                        <div className="zList">
                                            <div className="zDis">
                                                <i className="activity_1"></i>
                                                <span className="activity">
                                                    <span>10</span>
                                                </span>
                                            </div>
                                            <div className="zDis">
                                                <i className="activity_2"></i>
                                                <span className="activity">
                                                    <span>201030185030</span>
                                                </span>
                                            </div>
                                            <div className="zDis zHid">
                                                <i className="activity_2"></i>
                                                <span className="activity">
                                                    <span>5</span>
                                                </span>
                                            </div>
                                            <div className="zDis zHid">
                                                <i className="activity_3"></i>
                                                <span className="activity">
                                                    <span>100.01</span>
                                                </span>
                                            </div>
                                            <div className="zDis zHid">
                                                <i className="activity_4"></i>
                                                <span className="activity">
                                                    <span></span>
                                                </span>
                                            </div>
                                        </div>
                                        <div className="count">
                                            <span>5</span>
                                            <span className="zDown">
                                                <i className="icon-triangle"></i>
                                            </span>
                                        </div>
                                    </div>
                                </div>
                            </li> */}
                            {merchantList}
                        </ul>
                    </div>
                </div>
                <Footer/>
            </div>
        )
    }
}
export default Home;



